<template>
  <div ref="map" class="map"></div>
</template>

<script lang="ts">
import { defineComponent, onMounted } from "vue"; // Vue 3 Vue import  defineComponent onMounted .
import "ol/ol.css";
import Map from "ol/Map";
import View from "ol/View";
import TileLayer from "ol/layer/Tile";
import OSM from "ol/source/OSM";

export default defineComponent({
  name: "MapComponent",
  setup() {
    onMounted(() => {
      //   
      const mapContainer = document.querySelector(".map") as HTMLElement;

      //   
      const mapLayer = new TileLayer({
        source: new OSM(),
      });

      //   
      const mapView = new View({
        center: [0, 0],
        zoom: 2,
      });

      //  
      const map = new Map({
        target: mapContainer,
        layers: [mapLayer],
        view: mapView,
      });
    });
  },
});
</script>

<style scoped>
.map {
  width: 100%;
  height: 400px;
}
</style>
